.headerWarp { position: fixed; top: 0; left: 0; width: 100%; z-index: 10;
    header { position: relative; background: #fff; z-index: 9; box-shadow: 8px 8px 4px rgb(155, 155, 155);
        .logo { position: absolute; top: 79%; left: 49%; transform: translate(-50%,-50%); }
        .rowgroup1 {
            .row { display: flex; height: 56px;
                   padding-top: 32px;
                .col1 { align-self: center;
                    ul li { display: inline-block; padding-left: 15px; padding-bottom: 5px;
                        &:hover { color: #047be0; transition: all 0.3s; }
                    }
                    ul li a { font-size: 16px; }
                }
                .col2 { align-self: center; position: relative; left: 20%;
                    ul li { display: inline-block; padding-left: 15px;
                        &:nth-of-type(4) { position: relative; left: 4%;
                            padding: 0 10px;
                        }
                    }
                }
            }
        }
        .rowgroup2 { background: #0f4b7e;
            .row { display: flex; height: 56px; color: #fff;
                .col3 { align-self: center;
                    .active { color: #f9df03; }
                    ul li { display: inline-block; margin-right: 38px;
                        &:hover { color: #fcd05b; transition: all 0.3s; }
                    }
                    ul li:nth-child(5) { padding-left: 240px; }
                }
                .col4 { align-self: center;
                    ul li { display: inline-block; font-size: 15px; opacity: 0.8;
                        &:hover { opacity: 1; transition: all 0.3s; }
                        a:nth-of-type(1) { padding-left: 10px; }
                    }
                }
            }
        }
    }
}