.homesecWrap { padding-top: 132px;
    .article2 { padding-top: 60px; background: #f3f4ee;
        h2 { text-align: center; font-size: 28px; font-weight: 200; }
        .tour_list { padding-top: 8%; position: relative; bottom: 70px; }
        .row { display: flex; padding-top: 40px;
            .right { display: flex; flex-direction: column; padding-left: 50px;
                .rightbottom { display: flex; flex-direction: row; padding-top: 30px;
                    div:last-child { padding-left: 27px; }
                }
            }
        }
    }
    .article3 {
        h2 { width: 100%; text-align: center; font-size: 28px;
            padding: 40px 0 30px 0; font-weight: 200;
            span { font-weight: bold; }
        }
        .sec3_box { width: 100%; text-align: center;
            .sec3 { width: 1075px; display: flex; margin: 0 auto;
                .video { width: 519px; height: 291px; }
                .yongpyong { 
                    img { padding: 0 8px; }
                }
            }
        }
    }
    .article4 {
        h2 { width: 100%; text-align: center; font-size: 28px; 
            margin: 100px 0 30px 0; font-weight: 200;
            span { font-weight: bold; }
        }
    }
    .article5 { width: 1090px; margin: 0 auto; padding-top: 40px;
        .contact_board_box { display: flex;
            .contact_board { border: 1px solid #ddd; padding: 15px 30px; display: flex; width: 715px;
                .cb1 { width: 250px; display: flex; align-items: center;
                    .ka_plus { padding-left: 10px; padding-bottom: 10px;
                        a:nth-of-type(1) {
                            p { color: #333; font-weight: bold; font-size: 18px;
                                img { padding-left: 3px; }
                            }
                        }
                        a:nth-of-type(2) {
                            p { color: #333; font-size: 15px; padding-top: 3px;
                                &:hover { color: #047be0; }
                            }
                        }
                    }
                }
                .cb2 { width: 220px; padding-top: 5px; text-align: center;
                    p { font-size: 18px; color: #333; }
                    strong { font-size: 15px; color: #285583; }
                }
                .cb3 { position: relative; left: 8%; top: 3%; text-align: right;
                    p { color: #285583; font-size: 13px; }
                }
            }
            .notice { width: 130px; background: #333; position: relative; left: 50px;
                a {
                    h2 { font-size: 22px; text-align: center; line-height: 111px; color: #fff; font-weight: normal; }
                    img { position: absolute; top: 10px; right: 10px; }
                }
            }
        }
    }
}