.footerWrap { margin-top: 60px;
    footer { background: #202b37; height: 335px;
        .fbox { display: flex; justify-content: space-between;
            .flogo { padding-top: 140px; }
            .company { position: relative; padding-top: 100px;
                ul { position: absolute; right: 0;
                    li { display: inline-block; word-spacing: 0.5em;
                        &:nth-child(1):before { display: none; }
                        &:before { content: 'ã…£'; margin-left: 10px; margin-right: 10px; color: #fff; vertical-align: middle; }
                        a { color: #fff; font-size: 14px; }
                    }
                }
                .comInfo { padding-top: 35px; line-height: 1.7;
                    p { font-size: 12px; text-align: right; color: #b0b9c3; }
                }
                .copyRight { padding-top: 18px;
                    p { font-size: 12px; text-align: right; color: #b0b9c3; }
                }
            }
        }
    }
}
